<template>
  <div class="sector-list">
    <!-- <div class="title">{{title}}</div> -->
    <!--业务板块列表展示-->
    <div class="biz-sector">
      <el-button
        plain
        size="small"
        type="default"
        round
        v-for="sector in sectors"
        :class="currentActive===sector.id?'active':''"
        :key="sector.id"
        v-text="sector.name"
        @click="click(sector.id)">
      </el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BizSectorList',
  props: {
    title: {
      type: String,
      required: true
    },
    sectors: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      currentActive: '1'
    }
  },
  methods: {
    click (id) {
      this.currentActive = id
      this.$emit('sectorClick', id)
    }
  }
}
</script>

<style scoped>
  .active{
    background: #1ea8f0;
    border-color: #1ea8f0;
    color: #fff;
    outline: none;
  }
  .el-button.is-plain:hover, .el-button.is-plain:focus{
    background: #1ea8f0;
    border-color: #1ea8f0;
    color: #fff;
  }
  .sector-list > .title {
    padding-left: 16px;
    height: 50px;
    line-height: 50px;
    font-size: 15px;
    border-bottom: solid 1px #eee;
  }

  .sector-list > .biz-sector {
    padding: 12px;
    border-bottom: 1px dashed #DDDDDD
  }

  .sector-list > .biz-sector > .el-button {
    margin: 5px 5px 5px 5px;
  }
  .sector-list > .biz-sector > .el-button:hover {
    color: white;
    background-color: #1ea8f0;
    border-color: #1ea8f0;
  }

</style>



